import { memo } from 'react'

import Navigation from '../navigation/Navigation'
import TextArea from '../../../styled-components/TextArea/index'

import './styles.scss'

const Third = ({ data, setData, isValid, setIsValid, onFinish, activeIndex, setActiveIndex }) => {

    return (
        <>
            <div className="question__wrapper">
                <div className="input__wrapper">
                    <div className="input_textarea_wrapper">
                        <label> ?</label>
                        <div>
                            <TextArea value={data.books} placeholder=" " border="3px solid" onChange={(e) => setData({ ...data, books: e.target.value })} />
                            <p>,      ?      ?</p>
                        </div>
                    </div>
                    <div className="input_textarea_wrapper">
                        <label>      ?</label>
                        <div>
                            <TextArea value={data.courses} placeholder=" " border="3px solid" onChange={(e) => setData({ ...data, courses: e.target.value })} />
                            <p> , ,  ?  ,       ?</p>
                        </div>
                    </div>
                    <div className="input_textarea_wrapper">
                        <label>  ?</label>
                        <div>
                            <TextArea value={data.sources} placeholder=" " border="3px solid" onChange={(e) => setData({ ...data, sources: e.target.value })} />
                            <p>  -        ? , , ,  - ,         !</p>
                        </div>
                    </div>
                    <div className="input_textarea_wrapper">
                        <label>    5 ?</label>
                        <div>
                            <TextArea value={data.expectionsIn5Years} placeholder=" " border="3px solid" onChange={(e) => setData({ ...data, expectionsIn5Years: e.target.value })} />
                            <p>     1 .      ,     !        ?</p>
                        </div>
                    </div>
                </div>
            </div>
            <Navigation
                positions={{ left: "/second", right: "/fourth" }}
                data={data}
                isValid={isValid}
                setIsValid={setIsValid}
                onFinish={onFinish}
                activeIndex={activeIndex}
                setActiveIndex={setActiveIndex} />
        </>
    )
}

export default memo(Third)