import { memo } from "react"

import Navigation from '../navigation/Navigation'
import TextArea from '../../../styled-components/TextArea/index'

import './styles.scss'

const Fourth = ({ data, setData, isValid, setIsValid, onFinish, activeIndex, setActiveIndex }) => {

    return (
        <>
            <div className="question__wrapper">
                <div className="input__wrapper">
                    <div className="input_textarea_wrapper">
                        <label></label>
                        <div>
                            <TextArea value={data.expectedSalary} placeholder=" " border="3px solid" onChange={(e) => setData({ ...data, expectedSalary: e.target.value })} />
                            <p>, ,        ? ..   ,    ,      !</p>
                        </div>
                    </div>
                    <div className="input_textarea_wrapper">
                        <label>?</label>
                        <div>
                            <TextArea value={data.comfortableSalary} placeholder=" " border="3px solid" onChange={(e) => setData({ ...data, comfortableSalary: e.target.value })} />
                            <p>     ?   ...</p>
                        </div>
                    </div>
                    <div className="input_textarea_wrapper">
                        <label>  </label>
                        <div>
                            <TextArea value={data.minimumSalaryForLife} placeholder=" " border="3px solid" onChange={(e) => setData({ ...data, minimumSalaryForLife: e.target.value })} />
                            <p> ,     .</p>
                        </div>
                    </div>
                </div>
            </div>
            <Navigation
                positions={{ left: "/third", right: "/fifth" }}
                data={data}
                isValid={isValid}
                setIsValid={setIsValid}
                onFinish={onFinish}
                activeIndex={activeIndex}
                setActiveIndex={setActiveIndex} />
        </>
    )
}

export default memo(Fourth)