import { memo } from "react"

import TextArea from '../../../styled-components/TextArea/index'
import Navigation from "../navigation/Navigation"

import './styles.scss'

const Second = ({ data, setData, isValid, setIsValid, onFinish, activeIndex, setActiveIndex }) => {

    return (
        <>
            <div className="question__wrapper">
                <div className="input__wrapper">
                    <div className="input_textarea_wrapper">
                        <label>    ?</label>
                        <div>
                            <TextArea value={data.participated} placeholder=" " border="3px solid" onChange={(e) => setData({ ...data, participated: e.target.value })} />
                            <p>   :      ?    ?</p>
                        </div>
                    </div>
                    <div className="input_textarea_wrapper">
                        <label>  </label>
                        <div>
                            <TextArea value={data.failures} placeholder=" " border="3px solid" onChange={(e) => setData({ ...data, failures: e.target.value })} />
                            <p>     ?        ?</p>
                        </div>
                    </div>
                    <div className="input_textarea_wrapper">
                        <label>     ?</label>
                        <div>
                            <TextArea value={data.ownProjects} placeholder=" " border="3px solid" onChange={(e) => setData({ ...data, ownProjects: e.target.value })} />
                            <p>, ,   ?</p>
                        </div>
                    </div>
                </div>
            </div>
            <Navigation
                positions={{ left: "/first", right: "/third" }}
                data={data}
                isValid={isValid}
                setIsValid={setIsValid}
                onFinish={onFinish}
                activeIndex={activeIndex}
                setActiveIndex={setActiveIndex} />
        </>
    )
}

export default memo(Second)