import { memo } from "react"

import Navigation from '../navigation/Navigation'
import TextArea from '../../../styled-components/TextArea/index'

import './styles.scss'

const Fifth = ({ data, setData, isValid, setIsValid, onFinish, activeIndex, setActiveIndex }) => {

    return (
        <>
            <div className="question__wrapper">
                <div className="input__wrapper">
                    <div className="input_textarea_wrapper">
                        <label>    ?</label>
                        <div>
                            <TextArea value={data.whereToStart} placeholder=" " border="3px solid" onChange={(e) => setData({ ...data, whereToStart: e.target.value })} />
                            <p>         ?</p>
                        </div>
                    </div>
                    <div className="input_textarea_wrapper">
                        <label>   ?</label>
                        <div>
                            <TextArea value={data.howNotToDo} placeholder=" " border="3px solid" onChange={(e) => setData({ ...data, howNotToDo: e.target.value })} />
                            <p>  ,   ,    .     ?</p>
                        </div>
                    </div>
                    <div className="input_textarea_wrapper">
                        <label>  ?</label>
                        <div>
                            <TextArea value={data.howToDo} placeholder=" " border="3px solid" onChange={(e) => setData({ ...data, howToDo: e.target.value })} />
                            <p>    ?        ?</p>
                        </div>
                    </div>
                    <div className="input_textarea_wrapper">
                        <label> / ?</label>
                        <div>
                            <TextArea value={data.anyQuestions} placeholder=" " border="3px solid" onChange={(e) => setData({ ...data, anyQuestions: e.target.value })} />
                            <p>,         .  ,     {";)"}</p>
                        </div>
                    </div>
                </div>
            </div>
            <Navigation
                positions={{ left: "/fourth", right: "/finish" }}
                data={data}
                isValid={isValid}
                setIsValid={setIsValid}
                onFinish={onFinish}
                activeIndex={activeIndex}
                setActiveIndex={setActiveIndex} />
        </>
    )
}

export default memo(Fifth)